import React, { useState } from "react"
import "./logIn.css"
import { graphql, useStaticQuery } from "gatsby"

import { Router, Link, navigate } from "@reach/router"
import { render } from "react-dom"
import Recommended from "../../components/recommended"

function Button(username, password) {
  const data = useStaticQuery(graphql`
    query MyQuery {
      site {
        siteMetadata {
          postInfo {
            password
            username
            posts {
              comments
              dateAndTime
              description
              location
              tags
            }
          }
        }
      }
    }
  `)
  const userData = data.site.siteMetadata.postInfo

  console.log("bla ", userData)
  return (
    <div>
      <button
        class="logButton"
        onClick={() => {
          console.log("bla111 ", username, password)
          let flag = false
          for (let i = 0; i < userData.length; i++) {
            console.log(
              "iteration: ",
              username,
              userData[i].username,
              password,
              userData[i].password
            )
            if (
              username.username == userData[i].username &&
              username.password == userData[i].password
            ) {
              flag = true
              navigate("/recommended", {
                state: { userData: userData[i] },
              })
            }
          }
          if (!flag) alert("Error: Wrong username or password")
        }}
      >
        LOG IN
      </button>
    </div>
  )
}
class LogIn extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      first: "",
      last: "",
    }
  }

  updateInput(e) {
    this.setState({ first: e.target.value })
  }
  updateLast(e) {
    this.setState({ last: e.target.value })
  }

  render() {
    console.log(this.state.first, this.state.last)
    return (
      <div class="content1">
        <Router>
          <Recommended path="recommended" />
        </Router>
        <div class="firstName">
          <label>First Name: </label>
          <input
            class="first"
            value={this.state.first}
            type="text"
            placeholder="First Name ..."
            onChange={this.updateInput.bind(this)}
          />
        </div>
        <div class="firstName">
          <label>Last Name: </label>
          <input
            class="first"
            value={this.state.last}
            type="text"
            placeholder="Last Name ..."
            onChange={this.updateLast.bind(this)}
          />
        </div>

        <Button username={this.state.first} password={this.state.last} />
      </div>
    )
  }
}
export default LogIn
